.App {
  text-align: center;
  height: 100%;
  margin: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image {
  height: 300px;
  width: 300px;
  border-radius: 50%;
  border: 5px solid black;

}

.home-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.links{
  list-style-type: none;
  text-decoration: none;
}

.title {
  font-size: 75px;
}

.icons {
  font-size: 50px;
  display: flex;
  justify-content: center;
}